.menu-item {
  background-color: transparent;
  border: none;
  border-radius: 0.4rem;
  color: #fff;
  cursor: pointer;
  height: 1.75rem;
  margin-right: 0.25rem;
  padding: 0.25rem;
  width: 1.75rem;

  &:hover,
  &.is-active {
    background-color: #303030;
  }
}
svg {
  fill: white;
  height: 100%;
  width: 100%;
}
